@import '../../base.scss';

.message-list {
  &__wrapper {
    width: calc(100% - 40px);
    margin: 0px 20px;
    flex-grow: 1;
    align-items: flex-end;
    display: flex;
  }

  &__day-separator {
    font-weight: 500;
    font-size: 12px;
    line-height: 12px;
    text-align: center;
    color: #999999;
    margin: 0px 0px 20px 0px;
  }

  &__message {
    color: #1e1e1e;
    font-size: 12px;
    line-height: 15px;
    max-width: 50%;
    width: fit-content;
    padding: 11px 11px 9px;
    border-radius: 12px;
    margin: 10px 0px;
    position: relative;
    font-weight: 500;
    font-style: normal;

    &_incoming {
      background: #f8f8f8;
      text-align: left;
    }

    &_outcoming {
      background: #e4edfd;
      text-align: right;
      margin-left: auto;
    }
  }
}

.message-time {
  /* position: absolute; */
  right: 12px;
  bottom: 12px;
  font-weight: 500;
  font-size: 9px;
  line-height: 9px;
  text-align: right;
  margin-top: 2px;

  &__outcoming {
    color: #107d9f;
  }

  &__incoming {
    color: #999999;
  }
}

.message__status {
  align-items: center;
  display: flex;
  justify-content: flex-end;

  &_read {
    width: 12px;
    height: 6px;
    background-image: url("data:image/svg+xml,%3Csvg width='12' height='6' viewBox='0 0 11 5' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cline y1='-0.5' x2='3.765' y2='-0.5' transform='matrix(0.705933 0.708278 -0.705933 0.708278 0.700195 2.33313)' stroke='%231242BD'/%3E%3Cline y1='-0.5' x2='5.6475' y2='-0.5' transform='matrix(0.705933 -0.708278 0.705933 0.708278 3.35828 5.00006)' stroke='%231242BD'/%3E%3Cline y1='-0.5' x2='5.6475' y2='-0.5' transform='matrix(0.705933 -0.708278 0.705933 0.708278 6.01587 5.00006)' stroke='%231242BD'/%3E%3C/svg%3E%0A");
  }
}



